import { Link } from 'react-router-dom';
import { getProductsQuantity } from '../../helpers/functions';
import { CategoryType } from '../../types/CategoryType';
import { Product } from '../../types/Product';
import { Spinner } from '../Spinner';

type PropTypes = {
  isLoading: boolean;
  products: Product[];
};

const categories: CategoryType[] = [
  {
    id: 1,
    name: 'Mobile phones',
    type: 'phones',
    imageUrl:
      '../_new/img/category-phones.png',
    imageBackground: '#fcdbc1',
  },
  {
    id: 2,
    name: 'Tablets',
    type: 'tablets',
    imageUrl:
      '../_new/img/category-tablets.png',
    imageBackground: '#8d8d92',
  },
  {
    id: 3,
    name: 'Accessories',
    type: 'accessories',
    imageUrl:
      '../_new/img/category-accessories.png',
    imageBackground: '#973d5f',
  },
];

export const Category: React.FC<PropTypes> = ({ isLoading, products }) => {
  if (isLoading) {
    return (
      <section className="page__section category">
        <Spinner />
      </section>
    );
  }

  return (
    <section className="page__section category">
      <div className="container">
        <h1 className="category__title">
          Shop by category
        </h1>
        <div className="category__container">
          {categories.map(category => {
            const {
              imageUrl,
              name,
              type,
              imageBackground,
              id,
            } = category;
            const quantity = getProductsQuantity(type, products);

            return (
              <div className="category__box" key={id}>
                <div className="category__image-box">
                  <Link
                    className="category__element"
                    to={type}
                  >
                    <img
                      src={imageUrl}
                      alt="category"
                      className="category__img"
                      style={{ backgroundColor: imageBackground }}
                    />
                  </Link>
                </div>
                <Link to={type} className="category__link">
                  <h3 className="categoty__name">{name}</h3>
                </Link>
                <Link to={type} className="category__link-quantity">
                  <p className="category__quantity">{`${quantity} models`}</p>
                </Link>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};
