import { useState } from 'react';
import './App.scss';
import { Routes, Route, Navigate } from 'react-router-dom';
import { Provider } from 'react-redux';
import { store } from './redux/store';
import { Homepage } from './Pages/Homepage/Homepage';
import { PhonesPage } from './Pages/PhonePage';
import { Navbar } from './Components/Navbar';
import { TabletPage } from './Pages/TabletPage';
import { AccessoriesPage } from './Pages/AccessoriesPage';
import { Favourites } from './Pages/Favourites';
import { Cart } from './Pages/Cart';
import { ProductPage } from './Pages/ProductPage';
import { SearchContext } from './variables/contexts';

const App = () => {
  const [query, setQuery] = useState<string>('');

  return (
    <Provider store={store}>
      <SearchContext.Provider value={{ query, setQuery }}>
        <div className="page__content">
          <Navbar />

          <Routes>
            <Route path="/" element={<Homepage />} />
            <Route path="/home" element={<Navigate to="/" replace />} />
            <Route path=":id" element={<ProductPage />} />
            <Route path="phones">
              <Route index element={<PhonesPage />} />
              <Route path=":id" element={<ProductPage />} />
            </Route>
            <Route path="tablets">
              <Route index element={<TabletPage />} />
            </Route>
            <Route path="accessories">
              <Route index element={<AccessoriesPage />} />
            </Route>
            <Route path="favourites">
              <Route index element={<Favourites />} />
            </Route>
            <Route path="cart">
              <Route index element={<Cart />} />
            </Route>
          </Routes>

          <footer className="footer page__section-footer">
            <div className="footer__left-box">
              <a
                href="/"
                className="logo"
              >
                <img
                  src="../img/Icons/LOGO.svg"
                  alt="my-phone-logo"
                  className="logo__img"
                />
              </a>
            </div>
            <div className="footer__center-box">
              <nav className="nav nav--mob-no-display">
                <ul className="nav__list">
                  <li className="nav__item">
                    <a href="/" className="nav__link">Github</a>
                  </li>
                  <li className="nav__item">
                    <a href="/" className="nav__link">Contacts</a>
                  </li>
                  <li className="nav__item">
                    <a href="/" className="nav__link">rights</a>
                  </li>
                </ul>
              </nav>
            </div>
            <div className="footer__right-box">
              <a href="/" className="footer__link-home">
                Back to top
              </a>
              <button type="button" className="button footer__button">
                <div className="footer__arr footer__arr--up"> </div>
              </button>
            </div>
          </footer>
        </div>
      </SearchContext.Provider>
    </Provider>
  );
};

export default App;
