import classNames from 'classnames';
import { Link, useSearchParams } from 'react-router-dom';
import { getSearchWith } from '../../helpers/searchHelpers';

type PropTypes = {
  totalItems: number;
  itemsPerPage: number;
  currentPage: number;
};

export const Pagination: React.FC<PropTypes> = ({
  itemsPerPage,
  totalItems,
  currentPage,
}) => {
  const pageNumbers = [];
  const pages = Math.ceil(totalItems / itemsPerPage);
  const [searchParams] = useSearchParams();

  // eslint-disable-next-line no-plusplus
  for (let i = 1; i <= pages; i++) {
    pageNumbers.push(i);
  }

  const setCurrentPage = (newCurrentPage: number) => {
    return getSearchWith(searchParams, {
      page: `${newCurrentPage}`,
    });
  };

  const firstPage = pageNumbers[0];

  return (
    <ul className="phones__box-select">
      <li
        className="button phone__button"
      >
        <Link
          to={{
            search: setCurrentPage(currentPage - 1),
          }}
          className={classNames(
            'button__arr button__arr--left',
            {
              phones__disabled: firstPage === currentPage,
            },
          )}
        />
      </li>

      {pageNumbers.map(number => (
        <Link
          key={number}
          to={{
            search: setCurrentPage(number),
          }}
          className={classNames(
            'phones__button',
            {
              'phones__button--active': currentPage === number,
            },
          )}
        >
          {number}
        </Link>
      ))}

      <li
        className="button phone__button"
      >
        <Link
          to={{
            search: setCurrentPage(currentPage + 1),
          }}
          className={classNames(
            'button__arr button__arr--right',
            {
              phones__disabled: currentPage
                === pageNumbers[pageNumbers.length - 1],
            },
          )}
        />
      </li>
    </ul>
  );
};
