import classNames from 'classnames';
import { useSelector } from 'react-redux';
import { useContext } from 'react';
import { NavLink } from 'react-router-dom';
import { SearchContext } from '../../variables/contexts';
import { RootState } from '../../redux/store';

type PropTypes = {
  to: string;
  text: string
};

export const PageNavLink: React.FC<PropTypes> = ({ to, text = '' }) => (
  <NavLink
    className={({ isActive }) => classNames(
      'nav__link',
      { 'nav__link--active': isActive },
    )}
    to={to}
  >
    {text}
  </NavLink>
);

export const Navbar = () => {
  const { query, setQuery } = useContext(SearchContext);

  const items = useSelector((state: RootState) => state.cart.items);
  const itemsInFavorite
    = useSelector((state: RootState) => state.favorite.itemInFavorite);

  return (
    <header className="header page__section-header">
      <div className="header__left-box">
        <a
          href="/"
          className="logo header__logo"
        >
          <img
            src="../img/Icons/LOGO.svg"
            alt="my-phone-logo"
            className="logo__img"
          />
        </a>
        <nav className="nav nav--mob-no-display">
          <ul className="nav__list">
            <li className="nav__item">
              <PageNavLink to="/" text="Home" />
            </li>
            <li className="nav__item">
              <PageNavLink to="/phones" text="Phones" />
            </li>
            <li className="nav__item">
              <PageNavLink to="/tablets" text="Tablets" />
            </li>
            <li className="nav__item">
              <PageNavLink to="/accessories" text="Accessories" />
            </li>
          </ul>
        </nav>
      </div>
      <div className="header__right-box">
        <div className="header__box-input">
          <input
            type="text"
            className="header__input"
            value={query}
            onChange={(event) => setQuery(event.target.value)}
          />
        </div>
        <NavLink
          to="/favourites"
          className={({ isActive }) => classNames(
            'header__box-item',
            { 'header__box-item--active': isActive },
          )}
        >
          <div
            className={classNames(
              {
                'header__box-wrapper-favorite': itemsInFavorite.length,
              },
            )}
            data-count-favorite={itemsInFavorite.length}
          >
            <img
              src="../img/Icons/heart.svg"
              alt="favorite"
              className="header__box-image"
            />
          </div>
        </NavLink>
        <NavLink
          to="/cart"
          className={({ isActive }) => classNames(
            'header__box-item',
            { 'header__box-item--active': isActive },
          )}
        >
          <div
            className={classNames(
              {
                'header__box-wrapper-cart': items.length,
              },
            )}
            data-count-cart={items.length}
          >
            <img
              src="../img/Icons/cart.svg"
              alt="cart"
              className="header__box-image"
            />
          </div>
        </NavLink>
      </div>
    </header>
  );
};
