import {
  useContext,
  useEffect,
  useRef,
  useState,
} from 'react';
import { useLocation, useSearchParams } from 'react-router-dom';
import { getProducts } from '../../api/products';
import { Product } from '../../types/Product';
import { Phones } from '../../Components/Phones';
import { Spinner } from '../../Components/Spinner';
import { getProductsByType } from '../../helpers/getProductsByType';
import { TypeOfProduct } from '../../types/TypeOfProduct';
import { SearchContext } from '../../variables/contexts';
import { SortLink } from '../../Components/SortLink/SortLink';
import { PaginateLink } from '../../Components/PaginateLink';
import { Pagination } from '../../Components/Pagination';

export const PhonesPage = () => {
  const [products, setProducts] = useState<Product[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [defaultValue, setDefaultValue] = useState<string>('Choose an option');
  const [openSort, setOpenSort] = useState<boolean>(false);
  const [openPagintation, setOpenPagination] = useState<boolean>(false);
  const menuRef = useRef<HTMLButtonElement>(null);
  const paginationRef = useRef<HTMLButtonElement>(null);
  const DAFAULT_PAGE = '1';
  const DEFAULT_ITEMS_PER_PAGE = 4;
  const location = useLocation();

  const [searchParams] = useSearchParams();
  const isReversed = searchParams.get('order') === 'desc';
  const sortBy = searchParams.get('sort') as keyof Product;
  const currentPage = +(searchParams.get('page') || DAFAULT_PAGE);
  const itemsPerPage = +(searchParams.get('perPage') || DEFAULT_ITEMS_PER_PAGE);

  const { query } = useContext(SearchContext);

  useEffect(() => {
    setIsLoading(true);
    getProducts()
      .then(receivedProducts => {
        setProducts(receivedProducts);
        setIsLoading(false);
      })
      .catch(() => {
      })
      .finally(() => {
      });
  }, [location.search]);

  useEffect(() => {
    const handleDocumentClick = (event: MouseEvent) => {
      if (menuRef.current
        && !menuRef.current.contains(event.target as HTMLElement)) {
        setOpenSort(false);
      }

      if (paginationRef.current
        && !paginationRef.current.contains(event.target as HTMLElement)) {
        setOpenPagination(false);
      }
    };

    document.addEventListener('click', handleDocumentClick);

    return () => {
      document.removeEventListener('click', handleDocumentClick);
    };
  }, []);

  const phones = getProductsByType(products, TypeOfProduct.Phones);

  const lastItemIndex = currentPage * itemsPerPage;
  const firstItemIndex = lastItemIndex - itemsPerPage;
  const fixedQuery = query.toLowerCase().trim();

  const visiblePhones = phones.filter(({ color, capacity, name }) => (
    name.toLowerCase().trim().includes(fixedQuery)
    || color.toLowerCase().trim().includes(fixedQuery)
    || capacity.toLowerCase().trim().includes(fixedQuery)
  ));

  visiblePhones.sort((a, b) => {
    switch (sortBy) {
      case 'name':
        return a[sortBy].localeCompare(b[sortBy]);
      case 'price':
        return +b[sortBy] - (+a[sortBy]);
      case 'year':
        return +b[sortBy] - (+a[sortBy]);
      default:
        return 0;
    }
  });

  if (isReversed) {
    visiblePhones.reverse();
  }

  const quantity = phones.length;
  const currentItem = visiblePhones.slice(firstItemIndex, lastItemIndex);

  return (
    <main className="phones page__main">
      <div className="container">
        {!isLoading ? (
          <>
            <div className="phones__root-box">
              <a href="/">
                <img
                  src="../img/Icons/home.svg"
                  alt="toHome"
                  className="phones__home-img"
                />
              </a>
              <img
                src="../img/Icons/arr-right-hover.svg"
                alt="arr-right"
                className="phones__arr-right"
              />
              <p className="phones__root-name">Phones</p>
            </div>
            <h1 className="phones__title">Mobile phones</h1>
            <div className="phones__information-wrapper">
              <p className="phones__quantity">
                {`${quantity} models`}
              </p>
              <p className="phone__information">
                {`Page ${currentPage} (items ${firstItemIndex + 1} - ${lastItemIndex} of ${quantity})`}
              </p>
            </div>
            <div className="phones__sort-buttons">
              <div className="phones__sort-left">
                <p className="phones__sort-title">
                  Sort by
                </p>
                <div className="phones__dropdown-trigger">
                  <button
                    ref={menuRef}
                    type="button"
                    className="phones__button-dropdown"
                    onClick={() => setOpenSort(!openSort)}
                  >
                    {defaultValue}
                    <img
                      src="../img/Icons/arr-down.svg"
                      alt="dropdown"
                      className="phones__drop-icon"
                    />
                  </button>
                  {openSort && (
                    <ul className="phones__dropdown-content">
                      <li>
                        <SortLink
                          sort="name"
                          title="Alphabetically"
                          setDefaultValue={setDefaultValue}
                        />
                      </li>
                      <li>
                        <SortLink
                          sort="price"
                          title="Price"
                          setDefaultValue={setDefaultValue}
                        />
                      </li>
                      <li>
                        <SortLink
                          sort="year"
                          title="Newest"
                          setDefaultValue={setDefaultValue}
                        />
                      </li>
                    </ul>
                  )}
                </div>
              </div>
              <div className="phones__sort-right">
                <p className="phones__sort-title">
                  Items on page
                </p>
                <div className="phones__dropdown-trigger">
                  <button
                    ref={paginationRef}
                    type="button"
                    className="
                phones__button-dropdown
                phones__button-dropdown--right
                "
                    onClick={() => setOpenPagination(!openPagintation)}
                  >
                    <span className="phones__button-text">{itemsPerPage}</span>
                    <img
                      src="../img/Icons/arr-down.svg"
                      alt="dropdown"
                      className="phones__drop-icon"
                    />
                  </button>
                  {openPagintation && (
                    <ul className="phones__dropdown-content">
                      {['4', '8', '16', 'All'].map(value => (
                        <li>
                          <PaginateLink
                            key={value}
                            value={value}
                            quantity={quantity}
                            defaultPage={DAFAULT_PAGE}
                          />
                        </li>
                      ))}
                    </ul>
                  )}
                </div>
              </div>
            </div>
          </>
        ) : (<Spinner />)}
        <div className="phones__cataloge">
          <ul className="grid">
            <Phones products={currentItem} isLoading={isLoading} />
          </ul>
        </div>
        <Pagination
          totalItems={quantity}
          itemsPerPage={itemsPerPage}
          currentPage={currentPage}
        />
      </div>
    </main>
  );
};
